header {
  display: -ms-flexbox;
  display: flex;
  background-color: #F79700;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  color: white;
}

header * {
  margin: 15px 20px;
}

header a {
  cursor: pointer;
}

@media (max-width: 645px) {
  header {
    -ms-flex-direction: column;
        flex-direction: column;
  }

  header * {
    margin-bottom: 5px;
    font-weight: 500;
  }

  header a {
    border-bottom: 1.5px solid white;
    margin-bottom: 20px;
  }
}


